declare var $:any 
import { Component } from '@angular/core';
import { Events, IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { HttpServicesProvider } from '../../../../providers/http-services';
import { PhoneDeviceProvider } from '../../../../providers/phone-device';
import { StorageProvider } from '../../../../providers/storage';
import { ToolsProvider } from '../../../../providers/tools';
import { WorkflowServiceProvider } from '../../../../providers/workflow-service';

/**
 * Generated class for the lmpjMeetinginfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmpjMeetinginfo',
  templateUrl: 'lmpjMeetinginfo.html',
})
export class LmpjMeetinginfoPage {
  public formdata=[
    {'name':'会议名称','index':'meetname','type':'text','disabled':true,"placeholder":'','starttoggle':true,'toggleindex':'respunitname','plantitle':'会议信息'},
    {'name':'会议时间','index':'meettime','type':'text','disabled':true,"placeholder":''},
    {'name':'会议类型','index':'meettypename','type':'text','disabled':true,"placeholder":''},
    {'name':'与会人员','index':'meetperson','indexId':'meetperson','type':'choosemore','indexnum':'meetpersonlength','arrayindex':'userlist','span':true,'placeholder':'请选择成员'},
    {'name':'主要决议','index':'description','type':'textarea',"placeholder":''},
    {'name':'立项资料','index':'filename','type':'uploadify','placeholder':'','busstype':'lmpjMeeting'},
    
  ];
  public forminfo = {};
  public tasklist = [];
  public grouplist = [];
  public id='';
  public wfstatusname='';
  // public btnhide = false;
  public pageControl ={}
  constructor(public events: Events,public storage: StorageProvider,public navCtrl: NavController,public toastCtrl: ToastController, public navParams: NavParams,public httpService:HttpServicesProvider,public workflow: WorkflowServiceProvider,
    public tools:ToolsProvider,
    public phoneDevice: PhoneDeviceProvider,) {
      this.phoneDevice.phoneBacktoPop(this.navCtrl);
      this.id =this.navParams.get('id');
      this.initinfo()
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad PiinoutinfoPage');
  }
  pageElementControl(data){
    this.pageControl = data;
  }
  async pageElement(wfstatus,wfid){// 初始化页面资源权限
    // var that = this;
    const data = await this.httpService.getPageControlElementAnsy("lmpjMeeting_form",wfstatus,wfid);
    this.pageElementControl(data);
    var array = this.httpService.pageDate(data);
    this.formdata = this.httpService.getpagedata(this.formdata,array);
   
  }
  chooseitem(item){
    if(item.index =='meetperson' ){//工作班组成员
       this.navCtrl.push('UserdialogPage',{
         typeid:'',
         type:'multiple',
         typename:item.index,
         // array:this.forminfo[item.index]
       });
       this.events.subscribe('user:userinfo', (data, time) => {
         console.log(data)
        
       })
     }
   }
  initstatus(status){
    this.httpService.getDictName("LMPJ_PROJECT_STATUS",status, res => {
      if (res !== "") {
       this.forminfo['wfstatusname'] =res;
      }
    })
  }
  initinfo(){
    var that = this;
      var url = "/query/danger/lmpjMeeting/findOne?id=" + this.id;
      this.httpService.get(url, null, (result) => {
        console.log(result)
        if (result.code == 0) {
          if (result.data.length > 0) {
            this.forminfo = result.data[0];
            this.pageElement(result.data[0].wfstatus,result.data[0].wfid);
            // this.inituser(result.data[0].sigman);
            // this.initlocation(result.data[0].location);
            this.initstatus(result.data[0].wfstatus);
            this.httpService.initDeptName(result.data[0].respunit,'respunitname',this.forminfo )
            this.httpService.initUserName(result.data[0].respperson,'resppersonname',this.forminfo )
            this.httpService.initUserName(result.data[0].checkperson,'checkpersonname',this.forminfo )
            // var _this = this;
            this.httpService.getDictName("LMPJ_MEET_TYPE", result.data[0].meettype, function (res) {
              if (res !== "") {
                that.forminfo['meettypename'] = res;
              }
            });
            
            
            if(this.forminfo['planfinishtime'] !=null && this.forminfo['planfinishtime'] !='' ){
              this.forminfo['planfinishtime'] = new Date(new Date(this.forminfo['planfinishtime']).getTime()+8*60*60*1000).toISOString();
            }
            this.workflow.workflowcheckAuth(this.forminfo,'page-lmpjMeetinginfo');//检查流程  
          }
        }
      });
  }
  submit(){
    if(this.tools.initform(this.formdata,this.forminfo)){
      if(this.forminfo['planfinishtime'] !=null && this.forminfo['planfinishtime'] !='' && this.forminfo['planfinishtime'] !=undefined ){
        this.forminfo['planfinishtime'] = this.forminfo['planfinishtime'].replace(/T/, ' ').replace(/Z/, '');
      }
      if(this.forminfo['realfinishtime'] !=null && this.forminfo['realfinishtime'] !='' && this.forminfo['realfinishtime'] !=undefined ){
        this.forminfo['realfinishtime'] = this.forminfo['realfinishtime'].replace(/T/, ' ').replace(/Z/, '');
      }
      if(this.forminfo['checktime'] !=null && this.forminfo['checktime'] !='' && this.forminfo['checktime'] !=undefined ){
        this.forminfo['checktime'] = this.forminfo['checktime'].replace(/T/, ' ').replace(/Z/, '');
      }
           this.httpService.postform('/lmpjMeeting/save', 'lmpjMeeting', this.forminfo, (res) => {
          if(res.code==0){
            this.initinfo();
            this.toastCtrl.create({
              position:'bottom',
              duration:1500,
              message:'保存成功',
            }).present();
          }else{
            this.toastCtrl.create({
              position:'bottom',
              duration:1500,
              message:'保存失败',
            }).present();
          }
        })
     return true;
  }else{
    return false;
  }
  
   
  }
  workflowStart() { //发送流程
    // this.saveStr = "true";
      this.submit();
      if(this.submit() != undefined && this.submit() !=false){
        if (this.forminfo['wfid'] != "" && this.forminfo['wfid'] != null) {
          this.navCtrl.push('WorkflowroutePage', { 'busstype': 'lmpjMeeting', 'bussid': this.id, 'wfid': this.forminfo['wfid'] });
        } else {
          //发送流程
          this.navCtrl.push('WorkflowstartPage', { 'busstype': 'lmpjMeeting', 'bussid': this.id });
        }
      }
      
   
    
  }
}



